import React, {FunctionComponent} from "react"
import "./style.css"

const CheckBoxAlternative: FunctionComponent<{
  description?: string,
  className?: string,
  type?: string,
  id?: string, name?: string, title: string, onClick?: () => {}
}> = (props) => {


  return <label className={`checkbox ${props.className}`}>
    <input {...props}
           data-box-title={props.title}
           onChange={e => {
             document.getElementsByTagName("body")[0].dispatchEvent(new Event("checkbox_update"));
           }}
           type={props.type || 'checkbox'}
           onClick={props.onClick}
           className="checkbox__trigger visuallyhidden"/>
    <span className="checkbox__symbol">
    <svg aria-hidden="true" className="icon-checkbox" width="28px" height="28px" viewBox="0 0 28 28" version="1"
         xmlns="http://www.w3.org/2000/svg">
      <path d="M4 14l8 7L24 7"/>
    </svg>
  </span>
  </label>
}

export default CheckBoxAlternative
