import React, {FunctionComponent} from 'react';
import style from './style.module.scss';
import BlueBlock from "../blueblock";
import CheckBoxAlternative from "../wizard/checkbox_alternative";

const CookieSetting: FunctionComponent<{ cookieName: string, title: string, description: string }> = ({title, cookieName, description}) => {
  return (
    <div className={`${style.cookieSetting}`}>
      <CheckBoxAlternative className={style.checkbox} title={cookieName} name={cookieName} id={cookieName}/>
      <h2>{title}</h2>
      <p>{description}</p>
    </div>
  );
};

interface Cookie {
  cookieName: string,
  title: string,
  description: string
}

const CookieSettings: FunctionComponent<{ cookies: Cookie[] }> = ({cookies}) => {
  return (
    <BlueBlock>
      <div className={`${style.cookieSettings}`}>
        {cookies.map(cookie => <CookieSetting {...cookie}/>)}
      </div>
    </BlueBlock>
  );
};

export default CookieSettings;
